.container {
  display: flex;
  justify-content: center;
  margin-top: 80px;
  margin-bottom: 150px;
}

.numbers {
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
}

.arrowButton {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.arrowButton:hover {
  opacity: 0.8;
}

.eachNumber {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 7px 7px;
  border-radius: 30px;
  cursor: pointer;
  width: 45px;
  height: 45px;
}

.eachNumber > button {
  background-color: transparent;
  border: none;
  color: #66fcf1;
  font-size: 25px;
  font-family: "Bebas Neue";
  margin: 0 10px;
}

.eachNumber:hover {
  background-color: #46a29f;
  cursor: pointer;
}

.eachNumber:hover > button {
  color: #fff;
  cursor: pointer;
}

.selected {
  background-color: #46a29f;
}

.eachNumber > button.selectedButton {
  color: #fff !important;
}
